import { css } from "goober";
import { useState, useRef } from "preact/hooks";
import { playerStore } from "../data";
import { usePlayerList } from "../hooks";
import { h } from "preact";

export function PlayerList() {
  const playerList = usePlayerList();

  const styles = css`
    width: 80%;
    min-height: 10rem;
    .min-height {
      min-height:30rem;
    }
    .me {
      background-color: rgba(0, 0, 0, 0.075);
      font-weight: 700;
    }
  `;
  if (!playerList || playerList.length === 0 )
    return (
      <div class={styles}>
        <p class="placeholder-glow min-height">
          <span class="placeholder col-12 min-height" />
        </p>
      </div>
    );
  return (
    <div class={styles}>
      <table class="table">
        <thead class="table-dark ">
          <tr>
            <th scope="col">#</th>
            <th scope="col">Name</th>
            <th scope="col" style={{ maxWidth: "4rem" }}>
              Score
            </th>
          </tr>
        </thead>
        <tbody>
          {playerList.map((player) => (
            <tr class={`${player.me ? "me" : ""}`} key={player.name}>
              <th scope="row">{player.rank}</th>
              <td>{player.name}</td>
              <td>{player.highScore}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
