import { css } from 'goober';
import { useState, useRef, useEffect } from 'preact/hooks';
import { sleep } from '../utils';
import { h } from "preact";


const messageTimeMs = 5000;

export function Quote({ quotes }) {
    const [current, setCurrent] = useState();
    const indexRef = useRef(0);
    const eleRef = useRef(null);
    const styles = css`
      transition: all 300ms;
      overflow: hidden;
    `;
    useEffect(() => {
      const fn = async () => {
        if (indexRef.current >= quotes.length) {
          indexRef.current = 0;
        }
        eleRef.current.style.transform = "scaleX(0)";
        await sleep(350);
        setCurrent(quotes[indexRef.current]);
        indexRef.current++;
        eleRef.current.style.transform = "scaleX(1)";
  
        setTimeout(fn, messageTimeMs);
      };
      fn();
    }, [quotes]);
  
    return (
      <span ref={eleRef} class={styles}>
        "{current}"
      </span>
    );
  }