import { useEffect, useState } from 'react';
import { dataEvents, getClosePlayers, playerStore } from './data';


export function usePlayer() {
    const [player, setPlayer] = useState(playerStore.getPlayer());
    useEffect(() => {
        function onPlayerUpdated() {
            const player = playerStore.getPlayer()
            setPlayer(player);
        }
        window.addEventListener(dataEvents.playerUpdated, onPlayerUpdated)
        window.addEventListener(dataEvents.playerIdUpdated, onPlayerUpdated)
        return () => {
            window.removeEventListener(dataEvents.playerUpdated, onPlayerUpdated);
            window.removeEventListener(dataEvents.playerIdUpdated, onPlayerUpdated)
        }
    }, [])
    return player;
}

export function usePlayerList() {
    const [playerList, setPlayerList] = useState([]);
    useEffect(() => {
        async function loadPlayers() {
            const players = await getClosePlayers();
  
            setPlayerList(players);
        }
        loadPlayers()
        window.addEventListener(dataEvents.playerUpdated, loadPlayers)
        window.addEventListener(dataEvents.playerIdUpdated, loadPlayers)
        return () => {
            window.removeEventListener(dataEvents.playerUpdated, loadPlayers);
            window.removeEventListener(dataEvents.playerIdUpdated, loadPlayers)
        }
    }, [])
    return playerList;
}