import { nextId } from "./utils";
import * as forage from "localforage";

const DEFAULT_TEAM_ID = "1nn2iuopnpd20x72be08zt";
const apiBaseUrl = "https://simon-says-api.glitch.me";
export const dataEvents = {
  playerUpdated: "PLAYER_UPDATED",
  playerIdUpdated: "PLAYER_ID_UPDATED",
};

const url = new URL(window.location.href);
export const teamId = url.searchParams.get("team") || DEFAULT_TEAM_ID;
const userIdKey = `teams/${teamId}/userId`;

const teamPlayersKey = `teams/${teamId}/players`;

const ps = {
  getPlayerKey() {
    return `teams/${teamId}/players/${this.getPlayerId()}`;
  },
  getPlayerId() {
    return getItem(userIdKey);
  },
  setPlayerId(playerId) {
    setItem(userIdKey, playerId);
    window.dispatchEvent(
        new Event(dataEvents.playerIdUpdated, {
          bubbles: true,
          cancelable: false,
        })
      );
  },
  setPlayer({name, highScore}) {
    const key = this.getPlayerKey();  
    const player = {id: this.getPlayerId(), name, highScore};
    setItem(key, player);
  },
  getPlayer() {
    const key = this.getPlayerKey();
    let player = getItem(key);
    if (!player) {
      player = {
        id: this.getPlayerId(),
        name: `user${Date.now()}`,
        highScore: 0,
      };
      setItem(key, player);
    }
    return player;
  },

  setHighScore(highScore) {
    const player = this.getPlayer();
    player.highScore = highScore;
    this.updatePlayer(player);
  },
  setName(name) {
    const player = this.getPlayer();
    player.name = name;
    this.updatePlayer(player);
  },
  updatePlayer(player) {
    setItem(this.getPlayerKey(), player);
    window.dispatchEvent(
      new Event(dataEvents.playerUpdated, { bubbles: true, cancelable: false })
    );
  },
  setUploaded() {
    const player = this.getPlayer();
    player.uploaded = true;
    this.updatePlayer(player);
  },
};

if (!ps.getPlayerId()) {
    const id = nextId();
    ps.setPlayerId(id);
  }


const db = {
  async setTeamPlayers(players) {
    players = players.sort((a, b) => {
      if (a.highScore < b.hightScore) return -1;
      if (a.highScore > b.highScore) return 1;
      return 0;
    });

    players.forEach((x, index) => x.rank = index +1)
    await forage.setItem(teamPlayersKey, players);
  },
  async setPlayerName(name) {
    const players = await this.getTeamPlayers();
    const currentPlayer = players.find((x) => x.name === name);
    if (currentPlayer) {
      currentPlayer.name = name;
    }

    await this.setTeamPlayers(players);
  },
  async getTeamPlayers() {
    return (await forage.getItem(teamPlayersKey)) || [];
  },
};

const api = {
  async createPlayer({ id, teamId, name, highScore }) {
    await fetchJson("POST", `${apiBaseUrl}/teams/${teamId}/players`, {
      id,
      teamId,
      name,
      highScore,
    });
  },
  async getPlayer(id = ps.getPlayerId()) {
    return await fetchJson(
      "GET",
      `${apiBaseUrl}/teams/${teamId}/players/${id}`
    );
  },
  async getAllPlayers() {
    return await fetchJson("GET", `${apiBaseUrl}/teams/${teamId}/players`);
  },
  async setPlayerName(name) {
    await fetchJson(
      "POST",
      `${apiBaseUrl}/teams/${teamId}/players/${ps.getPlayerId()}/set-name`,
      name
    );
    ps.setName(name);
  },
  async setPlayerHighScore(score) {
    await fetchJson(
      "POST",
      `${apiBaseUrl}/teams/${teamId}/players/${ps.getPlayerId()}/set-high-score`,
      score
    );
  },
};


export const playerStore = ps;


export async function changePlayerId(playerId) {
    const remotePlayer = await api.getPlayer(playerId)
    ps.setPlayerId(playerId);
    ps.setPlayer(remotePlayer);
    dispatchEvent(dataEvents.playerIdUpdated);
}

export const setPlayerHighScore = async (score) => {
    ps.setHighScore(score);
    await api.setPlayerHighScore(score)
} 

export const setPlayerName = async (name) => {
    await api.setPlayerName(name);
}

export async function getClosePlayers() {
    await isInitialised;
    const count = 5;
    const players = await db.getTeamPlayers();
    const myIndex = players.findIndex((x) => x.me);
    if (myIndex >= 0) {
      let low = myIndex - count;
      let high = myIndex + count;
      low = low < 0 ? 0 : low;
      high = high > players.length - 1 ? players.length - 1 : high;
      return players.slice(low, high + 1);
    }
    return [];
  }

export const isInitialised = (async function load() {
  try {
    const remotePlayer = await api.getPlayer();
    const localPlayer = ps.getPlayer();

    if (localPlayer.highScore > (remotePlayer.highScore || 0)) {
      await api.setPlayerHighScore(localPlayer.highScore);
    } else if (localPlayer.highScore < remotePlayer.highScore) {
      ps.setHighScore(remotePlayer.highScore);
    }
    ps.setName(remotePlayer.name);
  } catch (err) {
    if (err.status === 404) {
      const player = ps.getPlayer();
      console.log("player does not exist");
      await api.createPlayer({
        id: ps.getPlayerId(),
        teamId,
        name: player.name,
        highScore: player.highScore,
      });
    }
  }
  const players = await api.getAllPlayers();
  await db.setTeamPlayers(players);
})();


export function dispatchEvent(type) {
    window.dispatchEvent(new Event(type, {bubbles: true, cancelable: false}))
}
function setItem(key, item) {
  window.localStorage.setItem(key, JSON.stringify(item));
}

function fetchJson(method, url, body) {
  const bodyStr = body ? JSON.stringify(body) : undefined;
  return fetch(url, {
    method,
    body: bodyStr,
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      Authorization: `bearer ${ps.getPlayerId()}`,
    },
  }).then((x) => {
    if (x.ok === false) throw x;
    return x.json();
  });
}

function getItem(key) {
  const str = window.localStorage.getItem(key);
  if (!str) {
    return null;
  }
  return JSON.parse(str);
}

export function updateHighScore() {}
