import { css } from "goober";
import { useEffect, useRef, useState } from "react";
import { changePlayerId, setPlayerHighScore, setPlayerName } from "../data";
import { usePlayer } from "../hooks";
import { PlayerList } from './PlayerList';
import SimonSays from "./SimonSays";

export function App() {
  const styles = css`
    display: grid;
    place-items: center;
    .body-content {
        margin-top:4rem;
        display:flex;
        gap: 2rem;
        flex-direction:column;
        width:100%;
        align-items:center;

    }
    .top-bar {
      display: flex;
      align-items: center;
      padding: 0 1rem;
      letter-spacing: 1px;
      color: white;
      height: 3rem;
      position: fixed;
      top: 0;
      right:0;
      left:0;
      z-index: 1000;
      background-color: #323232;
      width: 100%;
      .high-score {
        font-weight: 500;
        font-size: 1.2rem;
        position: absolute;
        right: 1rem;
      }
      .player-name {
        font-weight: 500;
        font-size: 1.2rem;
        .icon {
          font-size: 0.8rem;
          position: relative;
          top: -0.4rem;
        }
        cursor: pointer;
        user-select: none;
      }
    }
  `;

  const [size, setSize] = useState(400);
  const player = usePlayer();
  function calculateSize() {
    const windowHeight = window.innerHeight - 80;
    let length =
      window.innerWidth < windowHeight ? window.innerWidth : windowHeight;
    let defaultWidth = length - 30;
    if (defaultWidth < 200) defaultWidth = 200;
    if (defaultWidth > 800) defaultWidth = 800;
    setSize(defaultWidth);
  }
  useEffect(() => {
    calculateSize();
    window.addEventListener("resize", calculateSize);
    return () => {
      window.removeEventListener("resize", calculateSize);
    };
  }, []);

  async function onGameOver(score) {
    if (score > player.highScore) {
      await setPlayerHighScore(score);
    }
  }

  return (
    <div class={styles}>
      <UpdatePlayer />
      <div class="top-bar">
        <button
          type="button"
          class="player-name"
          data-bs-toggle="modal"
          data-bs-target="#change-player-name-modal"
        >
          {player?.name} <i class="bi bi-pencil-square icon" />
        </button>

        <span class="high-score">High Score: {player?.highScore}</span>
      </div>
      <div class="body-content">
        <SimonSays size={size} onGameOver={onGameOver} />
        <PlayerList />
      </div>
    </div>
  );
}

function UpdatePlayer() {
  const eleRef = useRef();
  const bsRef = useRef();
  const [name, setName] = useState("");

  const [newUserId, setNewUserId] = useState("");
  const [saving, setSaving] = useState(false);
  const [changeId, setChangeId] = useState(false);
  const player = usePlayer();
  const nameTooLong = name.length > 15;
  const nameMissing = !name.trim();
  useEffect(() => {
    // eslint-disable-next-line no-undef
    bsRef.current = bootstrap.Modal.getOrCreateInstance(eleRef.current);
    eleRef.current.addEventListener("hidden.bs.modal", () => {
      setName("");
    });

    eleRef.current.addEventListener("show.bs.modal", () => {
      setName(player.name);
      setNewUserId("");
      setChangeId(false);
      setSaving(false);
    });
  }, [player.name]);

  async function changePlayerName(name) {
    await setPlayerName(name);
  }

  async function save(e) {
    e.preventDefault();
    await setSaving(true);
    try {
      if (changeId) {
        await changePlayerId(newUserId);
      } else {
        if (nameTooLong && nameMissing) return;

        await changePlayerName(name);
      }

      bsRef.current.hide();
    } catch (error) {
      console.error(error);
    } finally {
      setSaving(false);
    }
  }

  const styles = css`
    .validation-error {
      color: red;
    }
    .user-id {
      margin-bottom: 1rem;
      font-style: italic;
      button {
        color: blue;
      }
    }
  `;
  return (
    <div
      ref={eleRef}
      class={`modal fade ${styles}`}
      id="change-player-name-modal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="change-player-name-label"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <form onSubmit={save}>
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="change-player-name-label">
                {changeId ? "Import a user id" : "Change your username"}
              </h5>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              />
            </div>
            <div class="modal-body">
              {!changeId && (
                <div class="form-group user-id">
                  <div>Your User Id: {player.id}</div>
                  <button type="button" onClick={() => setChangeId(true)}>
                    change
                  </button>
                </div>
              )}
              {changeId ? (
                <div class="form-group">
                  <label for="input-user-id">User Id To Import</label>
                  <input
                    type="text"
                    class="form-control"
                    id="input-user-id"
                    value={newUserId}
                    onChange={(x) => setNewUserId(x.target.value)}
                    placeholder="Enter new user id"
                  />
                </div>
              ) : (
                <div class="form-group">
                  <label for="input-username">Username</label>
                  <input
                    type="text"
                    class="form-control"
                    id="input-username"
                    value={name}
                    onChange={(x) => setName(x.target.value)}
                    placeholder="Enter username"
                  />
                  {nameTooLong && (
                    <small class="validation-error">
                      name be 15 chars or less
                    </small>
                  )}
                  {nameMissing && (
                    <small class="validation-error">name is required</small>
                  )}
                </div>
              )}
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                disabled={saving}
                data-bs-dismiss="modal"
              >
                Close
              </button>
              <button type="submit" disabled={saving} class="btn btn-primary">
                Save changes
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}
