import { h } from "preact";
import { css, setup } from "goober";
import {
  gameOverSound,
  giveMeSomeMoves,
  playAudio,
  sleep,
  timer,
} from "../utils";
import { useCallback, useEffect, useRef, useState } from "preact/hooks";
import { predefinedQuotes } from "../quotes";
import { colors } from '../style';
import { CenterConsole } from './CenterConsole';
import { GameButton } from './GameButton';
import { Quote } from './Quote';
setup(h);





export default function SimonSays({ size, quotes, onGameOver } = {}) {
  size = size || 400;
  quotes = quotes && quotes.length > 0 ? quotes : predefinedQuotes
  const styles = css`
    @import url("https://fonts.googleapis.com/css2?family=Sarpanch:wght@800&display=swap");
    ${reset}
    --size: ${size}px;
    position: relative;
    width: var(--size);
    height: var(--size);
    background-color: ${colors.black};
    border-radius: 999px;
    font-family: Sarpanch;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;

    *,
    *:before,
    *:after {
      user-select: none;
    }

    .button-container {
      display: grid;
      place-content: center;
    }
    .outer-ring {
      width: 100%;
      height: 100%;
      position: absolute;
      border: ${size * 0.04}px solid ${colors.black};
      border-radius: 999px;
      z-index: 100;
      pointer-events: none;
    }

    .vertical-border {
      position: absolute;
      height: 95%;
      z-index: 50;
      top: 50%;
      left: 50%;
      width: ${size * 0.07}px;
      transform: translate(-50%, -50%);
      background-color: ${colors.black};
    }

    .horizontal-border {
      position: absolute;
      display: grid;
      place-content: center;
      width: 95%;
      z-index: 150;
      top: 50%;
      left: 50%;
      height: ${size * 0.07}px;
      transform: translate(-50%, -50%);
      color: white;
      font-size: ${size * 0.03}px;
      text-align: center;
      font-style: italic;
      background-color: ${colors.black};
    }
  `;


  const selectionsRef = useRef([])
  const [entered, setEntered] = useState([]);
  const [gameStarted, setGameStarted] = useState(false);
  const disableButtonsRef = useRef(true);
  const movesRef = useRef([]);
  const timerRef = useRef();
  const [active, setActive] = useState();

  async function startGame() {
    if (gameStarted) return;
    resetGame();
    setGameStarted(true);
    selectionsRef.current = [];
    movesRef.current = giveMeSomeMoves(100);
    console.log('start');
    nextTurn();
  }

  const gameOver = useCallback(  async () => {
    async function gameOverAnimation() {
      const actions = [1,2,4,3];
      for (let j = 0; j < 3; j++) {
        for (let i = 0; i < 4; i++) {
          setActive(actions[i]);
          await sleep(80);
        }
      }    
      setActive(null);
    }
    
    timerRef.current?.stop()
    
    disableButtonsRef.current = true;
    gameOverSound.play();
    setGameStarted(false);
    await gameOverAnimation();
    onGameOver && onGameOver(selectionsRef.current.length)
  }, [onGameOver])
  
  const nextTurn = useCallback(async () => {
    selectionsRef.current.push(movesRef.current.pop());
    disableButtonsRef.current = true;
    for (const sel of selectionsRef.current) {
      setActive(sel);
      playAudio(sel);
      await sleep(400);
      setActive(null);
      await sleep(50);
    }
    disableButtonsRef.current = false;
    timerRef.current = timer(3000, gameOver);
    setEntered([]);
  }, [gameOver]);

  function optionClick(position) {
    setEntered((x) => {
      if(x.length === selectionsRef.current.length) return x;
      return [...x, position]
    });
  }

  function resetGame() {
    setGameStarted(false);
    disableButtonsRef.current = false;
    selectionsRef.current = [];
    setEntered([]);
  }


  useEffect(() => {
    if (!gameStarted) return;
    // when starting game while data loading. data loaded can trigger dep change which leads to 
    // next turn firing twice 
    if (selectionsRef.current.length ==0) {
      return;
    }
    const enteredStr = entered.join();
    const selectionsStr = selectionsRef.current.join();
    timerRef.current?.reset();
    if (enteredStr && selectionsStr.startsWith(enteredStr) === false) {
      return gameOver();
    }

    if (entered.join() === selectionsRef.current.join()) {
      timerRef.current?.stop();
      disableButtonsRef.current = true;
      setTimeout(() => {
        nextTurn();
      }, 800);
    }

  }, [entered, gameOver, gameStarted, nextTurn]);

  return (
    <div class={styles}>
      <CenterConsole
        size={size * 0.8}
        disabled={gameStarted}
        score={selectionsRef.current.length}
        onClick={startGame}
      />
      <div class="outer-ring" />
      <div class="vertical-border" />
      <div class="horizontal-border">
        <Quote quotes={quotes} />
      </div>
      <div class="button-container">
        <GameButton
          position={1}
          disabledRef={disableButtonsRef}
          activate={active === 1}
          onClick={optionClick}
          size={size}
        />
      </div>
      <div class="button-container">
        <GameButton
          position={2}
          disabledRef={disableButtonsRef}
          activate={active === 2}
          onClick={optionClick}
          size={size}
        />
      </div>
      <div class="button-container">
        <GameButton
          position={3}
          disabledRef={disableButtonsRef}
          activate={active === 3}
          onClick={optionClick}
          size={size}
        />
      </div>
      <div class="button-container">
        <GameButton
          position={4}
          disabledRef={disableButtonsRef}
          activate={active === 4}
          onClick={optionClick}
          size={size}
        />
      </div>
    </div>
  );
}





const reset = `
/*
  1. Use a more-intuitive box-sizing model.
*/
*, *::before, *::after {
  box-sizing: border-box;
}
/*
  2. Remove default margin
*/
* {
  margin: 0;
}

/*
  6. Improve media defaults
*/
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}
/*
  7. Remove built-in form typography styles
*/
input, button, textarea, select {
  font: inherit;
}
/*
  8. Avoid text overflows
*/
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}
/*
  9. Create a root stacking context
*/
#root, #__next {
  isolation: isolate;
}
`;
